Meteor.publish("News",function(typeID,sort,limit){
	// check(typeID,String);
	// check(sort,Object);
	// check(limit,Number);
	
	//æ–°é—»åˆ—è¡¨
	return NewsCol.find(
					{
						isVaild:1,
						typeObj:{
							$elemMatch:{
								typeID: new Meteor.Collection.ObjectID(typeID)
							}
						}
					},
					{
						sort: sort, limit: limit
					}
				);
});

